body{
    padding: 0;margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    /* background-color: aliceblue; */
}
.search{
    /* position: absolute;
    top: 30px;   */
    margin-left: 50px;
    margin-bottom: 10px;
}
.search input{
    padding: 5px 10px;
    border-radius: 10px;
    
    outline:none;
    border: 1px solid gray;
    text-align:justify;
}
.card{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1rem;

    width: 300px;
    height: 400px;
    /* background-color: rgb(29, 26, 26); */
    border-radius: 1rem;
    
    color: white;
    box-shadow: 5px 5px 5px gray;
    text-align: center;
    position: relative;
    
}
.card::before{
    content: "";
    background: url("https://png.pngtree.com/thumb_back/fh260/background/20210207/pngtree-black-background-light-simple-image_556933.jpg")no-repeat center center/cover ;
    position: absolute;
    width: 300px;
    height: 400px;
    z-index: -1;
    opacity: 1;
    border-radius: 1rem;

}
